import { useHistory } from "react-router-dom";
import { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";

export function InstrumentListPage() {
  const [instruments, setInstruments] = useState([]);
  const [isFetchPending, setFetchPending] = useState(false);
  const history = useHistory();
  const [deleteId, setDeleteId] = useState("");

  const fetchAll = () =>
    fetch(process.env.REACT_APP_BACK_URL + "/api/instruments")
      .then((res) => res.json())
      .then((hangszerek) => setInstruments(hangszerek))
      .catch(console.log);

  useEffect(() => {
    setFetchPending(true);
    fetchAll().finally(() => {
      setFetchPending(false);
    });
  }, []);

  return (
    <div className="p-5  m-auto text-center content bg-ivory">
      {deleteId !== "" ? (
        <Modal
          onApproved={async () => {
            setFetchPending(true);
            await fetch(`${process.env.REACT_APP_BACK_URL}/api/instruments/${deleteId}`, { method: "DELETE" });
            setDeleteId("");
            fetchAll().finally(() => {
              setFetchPending(false);
            });
          }}
          onClosed={() => setDeleteId("")}
        >
          Biztosan törlöd a hangszert?
        </Modal>
      ) : (
        ""
      )}

      {isFetchPending ? (
        <div className="spinner-border"></div>
      ) : (
        <div>
          <div className="jumbotron">
            <h1>Kódbázis Hangszerbolt™</h1>
            <small style={{ fontSize: 12 }}>
              <i>Alapítva: 2021</i>
            </small>
          </div>
          {alignToRows(instruments).map((row, i) => (
            <div key={i} className="row">
              {row.map((instrument) => (
                <div key={instrument.id} className="col-lg-4">
                  <div className="card w-100 d-inline-block m-1 p-2">
                    <h5 className="text-muted">{instrument.brand}</h5>
                    <h3 className="text-dark">{instrument.name}</h3>
                    <h5>{instrument.price} Ft -</h5>
                    <div className="small">Készleten: {instrument.quantity} db</div>
                    <div className="card-body text-dark">
                      <span style={{ maxHeight: 200 }}>{instrument.description}</span>
                    </div>
                    <div class="card-footer">
                      <NavLink key={instrument.id} to={"/hangszer-szerkesztese/" + instrument.id}>
                        <button className="btn btn-sm btn-warning mr-2">Szerkesztés</button>
                      </NavLink>

                      <button
                        className="btn btn-sm btn-danger mr-2"
                        type="button"
                        onClick={() => {
                          setDeleteId(instrument.id);
                        }}
                      >
                        Törlés
                      </button>
                      <NavLink to={"/hangszer/" + instrument.id}>
                        <button className="btn btn-sm btn-primary mr-2">Megtekintés</button>
                      </NavLink>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

const alignToRows = (items) =>
  items.slice().reduceRight((acc, curr, i, arr) => {
    acc.push(arr.splice(0, 3));
    return acc;
  }, []);

export function Modal({ onClosed, onApproved, children }) {
  return (
    <div className="modal d-block">
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">Figyelem!</h5>
            <button type="button" className="close" onClick={onClosed}>
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body">{children}</div>
          <div className="modal-footer">
            <button type="button" className="btn btn-primary" onClick={onApproved}>
              Megerősítés
            </button>
            <button type="button" className="btn btn-secondary" onClick={onClosed}>
              Vissza
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
