import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
export function InstrumentEditPage(props) {
  const id = props.match.params.hangszerId;
  const [instrument, setInstrument] = useState({});
  const [isPending, setPending] = useState(false);

  useEffect(() => {
    setPending(true);
    (async () => {
      try {
        const res = await fetch(`${process.env.REACT_APP_BACK_URL}/api/instruments/${id}`);
        const hangszer = await res.json();
        setInstrument(hangszer);
      } catch (e) {
        console.log(e);
      } finally {
        setPending(false);
      }
    })();
  }, []);

  const history = useHistory();
  return (
    <div className="p-5 content bg-whitesmoke text-center">
      <h2>Hangszer szerkesztése</h2>
      <form
        onSubmit={(e) => {
          e.persist();
          e.preventDefault();
          fetch(process.env.REACT_APP_BACK_URL + "/api/instruments/" + id, {
            method: "PATCH",
            body: JSON.stringify({
              name: e.target.elements.name.value,
              price: e.target.elements.price.value,
              quantity: e.target.elements.quantity.value,
              description: e.target.elements.description.value,
            }),
          })
            .then(() => {
              history.push("/");
            })
            .catch(console.log);
        }}
      >
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Név:</label>
          <div className="col-sm-9">
            <input type="text" name="name" className="form-control" defaultValue={instrument.name} />
          </div>
        </div>
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Ár:</label>
          <div className="col-sm-9">
            <input type="number" name="price" className="form-control" defaultValue={instrument.price} />
          </div>
        </div>
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Darabszám:</label>
          <div className="col-sm-9">
            <input type="number" name="quantity" className="form-control" defaultValue={instrument.quantity} />
          </div>
        </div>
        <div className="form-group row pb-3">
          <label className="col-sm-3 col-form-label">Leírás:</label>
          <div className="col-sm-9">
            <textarea rows="10" name="description" className="form-control" defaultValue={instrument.description}></textarea>
          </div>
        </div>
        <button type="submit" className="btn btn-success">
          Küldés
        </button>
      </form>
    </div>
  );
}
